<template>
  <div>
    <el-form label-width="80px" :model="form" ref="ruleForm" :rules="rules">
      <el-form-item label="使用说明" prop="content">
        <el-input
          v-model="form.content"
          type="textarea"
          :rows="6"
          placeholder="请输入内容"
        ></el-input>
      </el-form-item>
    </el-form>
    <el-button type="primary" @click="submitForm('ruleForm')" size="mini"
          >提 交</el-button
        >
  </div>
</template>

<script>
export default {
  name: "Point-management",
  components: {},
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      form: {
        content: "",
      },
      rules: {
        content: [       
          {
            min: 1,
            max: 400,
            message: "长度在 1 到 400 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {},
  mounted() {
    this.getData()
  },
  methods: {
    getData(){
      this.$reqGet("/point/rule").then((res) => {
        if (res.code == 200) {
          this.form=res.data?res.data:{}
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let url = this.form.id
            ? "/point/editRule"
            : "/point/addRule";
          this.$reqPost(url, {
            ...this.form,
          }).then((res) => {
            if (res.code == 200) {       
              this.$message.success('提交成功');  
              this.getData()
            } else {
              this.$message.error(res.msg);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
};
</script>
<style scoped></style>
